@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap');

:root{
    --resized-width: 250px;
    --desktop:1200px;
    --laptop:1024px;
    --tablet:768px;
    --phone:480px
}


.menu{
    position:fixed;
    color:yellow;  
    right:-300px;
    top:60px;
    z-index:10;
    transition:1s;
    width:300px;
    height:100vh;
    background:#313c4e;
    display:flex;
    justify-content:center;
    align-items:center;

}
.menu.active{
    right:0;
}
ul {
    list-style-type:none;
}
ul > a{
    text-decoration:none;
    color:rgba(255,255,255, 0.7);
    font-size:2rem;
    font-family: 'Quicksand', sans-serif;
}
ul > a > li{
    margin-top:60px;
    transition:0.3s;
    position:relative;
    bottom:50px;
    text-align:center;
}
ul > a > li:hover{
  color:white;
}
@media (max-width:1024px){
    .menu{
        right:-172px;
    }
    .menu.active{
        right:265px;
    }
}