@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;600&display=swap');

:root{
  --desktop:1200px;
  --laptop:1024px;
  --tablet:768px;
  --phone:480px
}

  .wrapper{
    height:calc(100vh - 25px);

  }
  .rightHome{
    width:50%;
    height:calc(100vh - 55px);
    float:right;
    position:relative;
    top:200px;
    right:80px;
  }
  
  .slapButton{
    color:white;
    cursor:pointer;
    width:200px;
    height:75px;
    outline:none;
    font-weight:100;
    transition:1s;
    font-weight:700;
    border:1.5px solid white;
    border-radius:3px;
    background:none;
    font-size:25px;
    position:absolute;
  }
  .slapButton > a{
      color:white;
      text-decoration: none;
  }
  .slapButton:hover{
      background:white;
      color:black;
  }

h1.fullName{
  font-family:'Poppins',sans-serif;
  margin-bottom:5px;
  font-size:45px;
}
span.occupation{
  font-weight:bold;
  font-size:35px;
  position:relative;
  top:85px;
 
  
}
.ityped-cursor {
  position:relative;
  top:85px;
  font-size:35px;
  opacity: 1;
  -webkit-animation: blink 0.3s infinite;
  -moz-animation: blink 0.3s infinite;
  animation: blink 0.3s infinite;
  animation-direction: alternate;
}

@keyframes blink {
  100% {
      opacity: 0;
  }
}

@-webkit-keyframes blink {
  100% {
      opacity: 0;
  }
}

@-moz-keyframes blink {
  100% {
      opacity: 0;
  }
}
.myFace{
  width:300px;
  position:absolute;
  top:30%;
  left:5%;
  border-radius:15px;
}

