.projectList{
    width:95%;
    /* background:#dfe7eb; */
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction:row;
    flex-wrap:wrap;
    height:auto;
    padding:35px;
    padding-left:60px;
    position:relative;
    bottom:80px;
}
/* white ish = #dfe7eb; */
.project > img{
    width:260px;
    margin-right:30px;
    cursor:pointer;
    border-radius:10px;
    transition:0.6s all ease;
    margin-bottom:30px;
}
 .project > img:hover{
    filter:brightness(0.3);
    transform:scale(1.1);
}


.redirectButton{
    position:absolute;
    transform:translate(-230px,65px);
    height:45px;
    width:160px;
    cursor:pointer;
    display:none;
}
.project > img:hover .redirectButton{
    display:block;
}
